<script>
    export default {
        name: "user-view",
        data() {
            return {
                window : window,
                model : {
                    payment : {},
                    transaction : {},
                },
                loading: true
            };
        },
        methods : {
            init(){
                this.loading = true;
                window.axios.get(`${window.location.origin}/admin/portal/zesa/${this.$route.params.id}/view`).then((response) => {
                    this.model = response.data.body.model;
                }).finally(() => {
                    this.loading = false;
                });
            },

            action(action){
                window.action(action , 'Zesa' , `${window.location.origin}/admin/portal/zesa/${this.$route.params.id}/${action}`).then((response) => {
                    this.model = response.data.body.model;
                });
            },
        },
        mounted() {
            this.init();
        }
    }
</script>
<template>
    <div :class="['dimmer' , loading ? 'active' : '']">
        <div class="loader"></div>
        <div class="dimmer-content">
            <div class="row">
                <div class="col-lg-12">
                    <div class="card ribbon-box ">
                        <div class="card-body">
                            <div class="d-flex align-items-center mb-3 flex-wrap">
                                <h4 class="header-title m-0 flex-fill">Zesa Information</h4>
                                <div class="">
                                    <button  @click="init" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-reload mr-1"></i> Refresh
                                    </button>
                                    <button v-if="!(model.state === 'COMPLETED')" @click="action('abort')" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-lightbulb-off mr-1"></i> Abort
                                    </button>
                                    <button v-if="!(model.state === 'COMPLETED')" @click="action('retry')" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-refresh mr-1"></i> Retry Token
                                    </button>
                                    <button v-if="!(model.state === 'COMPLETED')" @click="action('reset')" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-link-off mr-1"></i> Reset Zesa For Queue
                                    </button>
                                    <button v-if="" @click="action('sms')"  type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-text mr-1"></i> Send Token SMS
                                    </button>
                                    <button v-if="" @click="action('email')"  type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-mail mr-1"></i> Send Token Email
                                    </button>
                                    <a v-if="model.pdf" :href="`${window.location.origin}/admin/portal/zesa/${$route.params.id}/download`" target="_blank"  type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-download mr-1"></i> Download Receipt
                                    </a>
                                    <button v-if="!model.pdf" @click="action('generate')"  type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-download mr-1"></i> Generate Receipt
                                    </button>
                                </div>
                            </div>
                            <div v-if="model.transaction" class="text-left">
                                <p>
                                    <strong class="mr-2">Transaction  :</strong>
                                    <router-link class="badge badge-light p-2 m-1 font-12" :to="`/admin/transactions/${model.transaction.id}/view`">Transaction - #{{ model.transaction.id }}</router-link>
                                </p>
                            </div>

                            <p>
                                <strong class="mr-2">State :</strong>
                                <span>{{ model.state }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Meter :</strong>
                                <span>{{ model.meter }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Token :</strong>
                                <span>{{ model.token }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Amount :</strong>
                                <span>{{ model.amount }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Rated Amount :</strong>
                                <span>{{ model.rated_amount }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Name :</strong>
                                <span>{{ model.name }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Address :</strong>
                                <span>{{ model.address }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Vat Amount :</strong>
                                <span>{{ model.vatAmount }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Levy  :</strong>
                                <span>{{ model.levy }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Tariff Ref :</strong>
                                <span>{{ model.tariff }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Energy Bought :</strong>
                                <span>{{ model.energyBought }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Receipt Number :</strong>
                                <span>{{ model.receiptNumber }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Energy Charge :</strong>
                                <span>{{ model.energyCharge }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">SMS :</strong>
                                <span>{{ model.sms }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Email :</strong>
                                <span>{{ model.email }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Attempts :</strong>
                                <span>{{ model.attempts }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Aborted :</strong>
                                <span>{{ model.abort }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Last Attempt :</strong>
                                <span>{{ model.last_attempt }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Error :</strong>
                                <span>{{ model.error }}</span>
                            </p>

                            <p>
                                <strong class="mr-2">Created :</strong>
                                <span>{{ model.created_at }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Last :</strong>
                                <span>{{ model.updated_at }}</span>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
